import React, { useState, useEffect } from "react";
import MainSection from "./componets/MainSection";
import Navigation from "./componets/Navbar";
import LoginForm from "./componets/LoginForm";
import loginService from "./services/login";
import postService from "./services/julkaisut";


function App() {
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [user, setUser] = useState(null);

  useEffect(() => {
    const loggedUserJSON = window.localStorage.getItem("loggedSomeappUser");
    if (loggedUserJSON) {
      const user = JSON.parse(loggedUserJSON);
      console.log(user);
      setUser(user);
      postService.setToken(user.token);
    }
  }, []);

  const handleLogin = async (event) => {
    event.preventDefault();

   try {
      console.log(loginService);
      const user = await loginService({
        username,
        password,
      });
      console.log(user);
      window.localStorage.setItem("loggedSomeappUser", JSON.stringify(user));
      postService.setToken(user.token);
      setUser(user);
      console.log(user.username);
      setUsername("");
      setPassword("");
     } catch (exception) {
       console.log("Wrong credentials");
     }
  };

  return (
    <div className="App">
      {user ? (
        <div>
          <Navigation setUser={setUser} />
          <MainSection user={user} />
        </div>
      ) : (
        <LoginForm
          handleLogin={handleLogin}
          password={password}
          username={username}
          setPassword={setPassword}
          setUsername={setUsername}
        />
      )}
    </div>
  );
}

export default App;
